<template>
    <div class="noticeBox">
        <div class="notice-eidt">
            <el-button type="primary" @click="edit">新 增</el-button>
        </div>
        <el-table :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column :type="item.prop" :width="item.width" v-for="(item, index) in tableRows" :key="index"
                :fixed="item.fixed" :label="item.label" :prop="item.prop">
                <template slot-scope="scope">
                    <div class="detail-color" v-if="item.prop==='action'">
                        <span :class="scope.row.status!=='sms'?'':'hasSend'"
                            @click.prevent="handRevoke(scope.row)">撤回</span>
                        <span :class="scope.row.status!=='sms'?'':'hasSend'" style="margin-left:10px;"
                            @click.prevent="handSend(scope.row)">发送短信</span>
                    </div>
                    <div v-else-if="item.prop=='index'">{{scope.$index+1}}</div>
                    <div v-else>{{scope.row[item.prop]}}</div>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="query.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="query.pageSize"
                layout="prev, pager, next, jumper,total, sizes" :total="total">
            </el-pagination>
        </div>
        <el-dialog title="停送电通知编辑" :visible.sync="dialogVisible" hight="600px" width="4.5rem"
            :before-close="handleClose" top="20vh">
            <el-form ref="form" :model="queryForm" label-width="1rem">
                <el-form-item label="通知标题：">
                    <el-input v-model="queryForm.name"></el-input>
                </el-form-item>
                <el-form-item label="通知种类：">
                    <el-select v-model="queryForm.type">
                        <el-option v-for="item in typeOptions" :key="item.label" :label="item.label"
                            :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="停电时间：" v-if="queryForm.type==1">
                    <el-date-picker v-model="queryForm.stopTime" :picker-options="pickerBeginDateBefore" type="datetime"
                        placeholder="选择日期时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="来电时间：">
                    <el-date-picker v-model="queryForm.resumeTime" :picker-options="pickerBeginDateBefore2"
                        type="datetime" placeholder="选择日期时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="影响区域：">
                    <el-input v-model="queryForm.scope"></el-input>
                </el-form-item>
                <el-form-item label="操作原因：">
                    <el-input type="textarea" v-model="queryForm.reason"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submit">提交</el-button>
                <el-button @click="reSet">重置</el-button>
                <el-button @click="cancle">取消</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import moment from 'moment'
    import {
        getNotices,
        cancleNotice,
        addNotice,
        sendSms
    } from '@/api/yw'
    import {
        mapState
    } from 'vuex'
    export default {
        name: 'bills',
        data() {
            return {

                query: {
                    pageNum: 1,
                    pageSize: 10,
                    userId: ''
                },
                total: 0,
                typeOptions: [{
                    label: '停电通知',
                    value: '1',
                }, {
                    label: '来电通知',
                    value: '2',
                }],
                tableData: [],
                tableRows: [{
                        width: '90px',
                        label: '序号',
                        prop: 'index',
                    },
                    {

                        label: '通知种类',
                        prop: 'type',
                    }, {

                        label: '停电时间',
                        prop: 'stopTime',
                    },
                    {

                        label: '原因',
                        prop: 'reason',
                    }, {

                        label: '影响区域',
                        prop: 'scope',
                    }, {

                        label: '恢复时间',
                        prop: 'resumeTime',
                    }, {
                        fixed: 'right',
                        width: 120,
                        label: '操作',
                        prop: 'action',
                    },
                ],
                dialogVisible: false,
                queryForm: {
                    type: '1',
                    name: '',
                    stopTime: '',
                    resumeTime: '',
                    scope: '',
                    reason: ''
                },
                pickerBeginDateBefore: {
                    disabledDate(time) {
                        return time.getTime() < Date.now() - 3600 * 1000 * 24
                    },

                },
                pickerBeginDateBefore2: {
                    disabledDate(time) {
                        return time.getTime() < Date.now() - 3600 * 1000 * 24
                    }
                }

            }
        },
        created() {
            this.getNotices();
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId,
            }),

        },
        methods: {
            getNotices() {
                this.query.userId = this.userId
                getNotices(this.query).then(res => {
                    this.tableData = res.rows ? res.rows.map(item => {
                        item.stopTime = item.stopTime ? moment(item.stopTime).format(
                            'YYYY-MM-DD HH:mm:ss') : '';
                        item.resumeTime = item.resumeTime ? moment(item.resumeTime).format(
                            'YYYY-MM-DD HH:mm:ss') : '';
                        item.type = item.type == 1 ? '停电通知' : '来电通知'
                        return item
                    }) : []
                    this.total = res.total
                })
            },
            handRevoke(item) {
                if (item.status == 'sms') return;
                this.$confirm('此操作将撤销该通知事件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    cancleNotice({
                        id: item.id
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: '撤销成功!'
                        });
                        this.getNotices()
                    }).catch(err => {
                        this.$message({
                            type: 'error',
                            message: '撤销失败'
                        });
                    })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                });

            },
            handSend(item) {
                if (item.status == 'sms') return;
                this.$confirm('此操作将发送短信通知, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    sendSms(item.id).then(res => {
                        this.$message({
                            type: 'success',
                            message: '发送成功!'
                        });
                        this.getNotices()
                    }).catch(err => {
                        this.$message({
                            type: 'error',
                            message: '发送失败'
                        });
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '取消发送短信'
                    });
                });
            },
            edit() {
                this.dialogVisible = true;
            },
            submit() {

                this.queryForm.stopTime = moment(this.queryForm.stopTime).format('YYYY-MM-DD HH:mm:ss')
                this.queryForm.resumeTime = moment(this.queryForm.resumeTime).format('YYYY-MM-DD HH:mm:ss')
                if (this.queryForm.type == 1) {
                    if (new Date(this.queryForm.stopTime) > new Date(this.queryForm.resumeTime)) {
                        this.$message({
                            type: 'error',
                            message: '停电时间不能晚于恢复时间'
                        })
                        return
                    }
                } else {
                    delete this.queryForm.stopTime
                }

                addNotice(this.queryForm).then(res => {
                    this.$message({
                        type: 'success',
                        message: '新增成功!'
                    });

                    this.dialogVisible = false;
                    this.getNotices()
                    this.reSet()
                })
            },
            reSet() {
                this.queryForm = {
                    type: '',
                    stopTime: '',
                    resumeTime: '',
                    scope: '',
                    reason: ''
                }
                this.dialogVisible = false;
            },
            cancle() {
                this.dialogVisible = false;
            },
            handleClose() {
                this.dialogVisible = false;
            },
            handleSizeChange(val) {
                this.query.pageSize = val
                this.getNotices()
                // console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.query.pageNum = val
                this.getNotices()
                // console.log(`当前页: ${val}`);
            },
            // changeTime(date) {
            //     var startAt = new Date(this.stopTime).getTime;
            //     if (startAt < Date.now()) {
            //         this.stopTime = new Date();
            //     }
            //     // this.pickerBeginDateBefore2 = {

            //     //     disabledDate(time) {
            //     //         return time.getTime() < new Date(date).getTime()
            //     //     }
            //     // }
            // },
            // changeTime2(date) {
            //     this.pickerBeginDateBefore = {
            //         disabledDate(time) {
            //             return time.getTime() > new Date(date).getTime()
            //         }
            //     }
            // },

        }
    }
</script>

<style scoped lang="scss">
    .notice-eidt {
        margin-top: 24px;
        margin-bottom: 16px;
    }

    .noticeBox {
        padding-bottom: 20px;

        ::v-deep .el-select {
            width: 100%;
        }

        ::v-deep .el-date-editor.el-input,
        .el-date-editor.el-input__inner {
            width: 100%;
        }
    }

    .detail-color {
        cursor: pointer;
        color: #2E4AF7;
    }



    .hasSend {

        cursor: not-allowed;
        color: #999
    }
</style>