<template>
    <div class="personal commpage">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>个人中心</span>
            </div>
            <div class="person-card">
                <div class="card-left">
                    <div>用户名称</div>
                    <div>{{info.nickname?info.nickname:'-'}}</div>
                </div>
                <div class="card-right">
                    <div>所属部门</div>
                    <div>{{ isFinanceRole? this.roles :( info.dept?info.dept:'-')}}</div>
                </div>
                <div class="card-left">
                    <div>所辖平台个数</div>
                    <div>{{info.platNum?info.platNum:'-'}}</div>
                </div>
                <div class="card-right">
                    <div>所辖计量点个数</div>
                    <div>{{info.jpointNum?info.jpointNum:'-'}}</div>
                </div>
                <div class="card-left">
                    <div>异常计量点个数</div>
                    <div>{{info.jpointYcNum||info.jpointYcNum==0?info.jpointYcNum:'-'}}</div>
                </div>
                <div class="card-right">
                    <div>上次登录时间</div>
                    <div>{{info.lastLoginTime? moment(info.lastLoginTime).format('YYYY-MM-DD HH:mm:ss') :'-'}}</div>
                </div>
            </div>
        </el-card>
        <div class="person-tabs">
            <div class="tabs-head">
                <el-tabs v-model="activeIndex">
                    <el-tab-pane label="停电通知" name="1">
                        <Notice></Notice>
                    </el-tab-pane>
                    <el-tab-pane label="账单信息处理" name="2">
                        <Bill></Bill>
                    </el-tab-pane>
                    <el-tab-pane label="客户留言处理" name="3">
                        <Leav></Leav>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </div>

    </div>
</template>

<script>
    import {
        getYwInfo
    } from '@/api/yw'
    import moment from 'moment'
    import {
        mapState
    } from 'vuex'
    import {
        ROLENAME
    } from "@/utils/config.js";
    import Bill from "./bill.vue";
    import Notice from "./notice.vue";
    import Leav from "./leav.vue";
    export default {
        name: 'index',
        components: {
            Notice, //停电通知
            Bill, //账单
            Leav,
        },
        data() {
            return {
                moment,
                activeIndex: '1', //默认激活第一项 
                info: {},
            }
        },
        created() {
            this.getYwInfo()
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId,
                isFinanceRole: state => state.permission.roles.includes(ROLENAME.finance),
                roles: state => state.permission.roles.join('/'),
            })
        },
        mounted() {},
        methods: {
            getYwInfo() {
                getYwInfo({
                    userId: this.userId
                }).then(res => {
                    this.info = res.data
                })
            }
        }
    }
</script>

