<template>
    <div class="leavBox">
        <div class="search-box">
            <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset">
            </form-create>
        </div>
        <el-table :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column type="index" label="序号" width="120"></el-table-column>
            <el-table-column v-for="(item, index) in tableRows" :width="item.width" :key="index" :label="item.label"
                :prop="item.prop">
                <template slot-scope="scope">
                    <div v-if="item.prop=='status'">{{statusObj[scope.row[item.prop]]}}</div>
                    <div v-else-if="item.prop=='crtTime'">
                        {{moment(scope.row[item.prop]).format('YYYY-MM-DD HH:mm:ss')}} </div>
                    <div v-else>{{scope.row[item.prop]}}</div>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="80" fixed="right">
                <template slot-scope="scope">
                    <div class="detail-color" v-if="scope.row['status']==0" @click="handRePaly(scope.row)">回复</div>
                    <div class="detail-color" v-else @click="handDetail(scope.row)">查看</div>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="query.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="query.pageSize"
                layout="prev, pager, next, jumper,total, sizes" :total="total">
            </el-pagination>
        </div>
        <el-dialog :title="title" :visible.sync="dialogVisible" hight="600px" width="450px" :before-close="handleClose"
            top="20vh">
            <el-form ref="form" :model="queryForm">
                <el-form-item>
                    <el-input disabled type="textarea" v-model="queryForm.content"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input type="textarea" v-model="queryForm.reply"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submit">提交</el-button>
                <el-button @click="cancle">取消</el-button>
            </span>
        </el-dialog>
        <el-dialog :title="title" :visible.sync="open" width="30%" :before-close="()=>{this.open = false}">
            <span>{{seeContent}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="open = false">取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import moment from 'moment'

    import {
        getRemark,
        rePlay
    } from '@/api/yw'
    export default {
        name: 'bills',
        data() {
            return {
                formObj: {},
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '70px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                //表单生成规则
                rule: [{
                    type: 'input',
                    field: "nickname",
                    title: '姓名：',
                    value: '',
                    props: {
                        placeholder: '请输入姓名',
                    },
                    col: {
                        span: 6,
                    }
                }, {
                    type: "DatePicker",
                    field: "time",
                    title: "时间段：",
                    value: [],
                    props: {
                        type: "datetimerange",
                        "range-separator": "至",
                        "start-placeholder": "开始日期",
                        "end-placeholder": "结束日期",
                        pickerOptions: { //时间选择器时间范围限定
                            disabledDate(time) {
                                const now = new Date();
                                now.setDate(now.getDate() + 1);
                                now.setMinutes(0);
                                now.setSeconds(0);
                                now.setMilliseconds(0);
                                return time.getTime() > now;
                            },
                        }
                    },
                    col: {
                        span: 8
                    }
                }, {
                    type: 'select',
                    field: "status",
                    title: '状态：',
                    value: '',
                    options: [{
                        value: '',
                        label: '全部',
                    }, {
                        value: '0',
                        label: '未处理',
                    }, {
                        value: '1',
                        label: '已处理',
                    }],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 4,
                    }
                }],
                moment,
                query: {
                    pageNum: 1,
                    pageSize: 10,
                    st: '',
                    et: '',
                    nickname: ''
                },
                total: 0,
                statusObj: {
                    '0': '未处理',
                    '1': '已处理',
                },
                title: '',
                tableData: [],
                tableRows: [{
                    label: '姓名',
                    width: 150,
                    prop: 'nickname',
                }, {
                    label: '所属单位',
                    prop: 'org',
                    width: 400,
                }, {

                    label: '时间',
                    prop: 'crtTime',
                }, {

                    label: '内容',
                    prop: 'content',
                }, {

                    label: '状态',
                    prop: 'status',
                }],
                dialogVisible: false,
                queryForm: {
                    content: '',
                    reply: '',
                },
                seeContent: '',
                open: false
            }
        },
        created() {
            this.getRemark();
        },

        mounted() {},
        methods: {
            getRemark() {
                getRemark(this.query).then(res => {
                    this.tableData = res.rows;
                    this.total = res.total
                })
            },
            handRePaly(item) { //点击回复
                this.title = '回复'
                this.queryForm = {
                    ...item
                }
                this.dialogVisible = true;
            },
            handDetail(item) {
                this.title = "查看信息"
                this.seeContent = item.content;
                this.open = true
            },
            search() {
                const formData = this.formObj.formData();
                this.query.st = formData.time[0];
                this.query.et = formData.time[1];
                this.query.nickname = formData.nickname;
                this.query.status = formData.status;
                this.getRemark();
            },
            submit() {
                rePlay(this.queryForm).then(res => {
                    this.$message({
                        type: 'success',
                        message: '回复成功!'
                    });
                    this.getRemark();
                    this.dialogVisible = false
                })
            },
            reset() {
                //重置表单
                this.formObj.resetFields();
                this.query = {
                    pageNum: 1,
                    pageSize: 10,
                    st: '',
                    et: '',
                    status: '',
                    nickname: ''
                };
                this.getRemark();
            },
            cancle() {
                this.dialogVisible = false;
            },
            handleClose() {
                this.dialogVisible = false;
            },
            handleSizeChange(val) {
                this.query.pageSize = val
                this.getRemark()
            },
            handleCurrentChange(val) {
                this.query.pageNum = val
                this.getRemark()
            },

        }
    }
</script>

<style scoped lang="scss">
    .leavBox {
        padding-bottom: 20px;
    }

    /deep/ .el-form-item {
        margin-bottom: 16px;
    }

    .search-box {
        position: relative;
        margin-top: 24px;
        // margin-bottom: 4px;

        .btn-box {
            position: absolute;
            z-index: 9;
            top: 0;
            right: 0;
        }
    }

    .detail-color {
        cursor: pointer;
        color: #2E4AF7;
    }

    .min-item,
    .max-item {
        ::v-deep .el-form-item__label {
            text-align: left;
        }
    }
</style>